
// NAV
.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-logo {
    width:20%;
    img{
        width: 75%;
    }
}

.nav-list{
    display: flex;
    li{
        list-style-type: none;
        padding:0 0.8rem;
        a{
            text-decoration: none;
            color: #519259;
        }
    }
}


// BANNER

.banner{
    background-image:linear-gradient(#ffffff75,#ffffff75), url('./img/banner.png');
    background-size: cover;
    background-attachment: fixed;
    z-index: 1;
    position: relative;

    .banner-content{
        padding: 10rem 0;
        width: 65%;
        margin: auto;
        h1{
            font-size: 2.5rem;
            text-align: start;
        }
        p{
            font-weight: 700;
            text-align: start;
        }
        button{
            padding: 0.7rem 2.3rem;
            background-color: #278233 ;
            color: #e5dcdc;
            border: none;
            display: flex;
            border-radius: 0.3rem;
        }
    }
}


.intro-sec{
    width: 90%;
    margin: auto;
    position: relative;
    margin-bottom: 1rem;
    h3{
        text-align: start;
        padding-left: 7rem;
        margin-bottom: 3rem;
    }

    h3::before{
        width: 0;
        position: absolute;
        left: 6rem;
        right: 0;
        content: "";
        border: 2px solid #519259;
        height: 1.5rem;
    }

    .side-img{
        width: 60%;
        position: absolute;
        top: -35%;
        left: 77%;
        transform: rotate(325deg);
        img{
            width: 28%;
        }
    }

    .intro-detail{
        display: flex;

        .intro-imgs{
            width: 40%;
            position: relative;

            .parent-img{
                width: 85%;
                img{
                    width: 50%;
                }
            }
            .parent-small-img{
                position: absolute;
                top: 0%;
                left: 15px;
                width: 81%;
                img{
                    width: 50%;
                }
            }

            .intro-parent-content-img{
                    
                .intro-small1-content-img{
                    position: absolute;
                    top: 7%;
                    left: 5.6rem;
                    width: 36%;
                    img{
                        width: 50%;
                    }   
                }

                .intro-small2-content-img{
                    position: absolute;
                    top: 47%;
                    left: 5.6rem;
                    width: 36%;
                    img{
                        width: 50%;
                    }   
                }
                
                .intro-small3-content-img{
                    position: absolute;
                    top: 6%;
                    left: 12rem;
                    width: 32%;
                    
                    img{
                        width: 50%;
                    }   
                }
            }
        }
        .intro-content{
            font-weight: 500;
            width: 40%;
            text-align: start;
            font-size: 1rem;
        }
    }
}

.footer-sec{
    display: flex;
    margin-bottom: 0.3rem;
    position: relative;

    .foot-left{
        text-align: start;
        width: 33.33%;
        padding: 0 6rem;

        .foot-img{
        width: 100%;
        }
        img{
            width: 65%;
        }
    }

    .foot-total-list{
        background-color:#F4EEA9;
        display:flex;
        width:100%;
        padding-bottom: 2rem;

        .foot-middle{
            width: 50%;
            text-align: start;

            h3{
                padding-left: 2.3rem;
            }
            ul{
                list-style-type: none;
                li{
                    padding-bottom: 0.3rem;
                }
            }
        }

        .foot-right{
            width: 50%;
            text-align: start;

            h3{
                padding-left: 2.3rem;
            }
            ul{
                list-style-type: none;
                li{
                    padding-bottom: 0.3rem;
                }
            }
        }
    }

}

.notes{
    position: absolute;
    top: 190%;
    left: 47%;
}